import React, { useState, useEffect } from "react";
import { ChevronDown, X } from "lucide-react";
import avtar from "../../../public/assets/avtar.jpg";
import Image from "next/image";
import axios from "axios";

interface memberInterface {
  id: string;
  fullName: string;
  role: string;
  position: string;
  avatar: string;
}

interface AddTeamMembersProps {
  onClose: () => void;
  onAddMember: (newMember: memberInterface) => void;
  teamMembers: memberInterface[];
}

const AddTeamMembers: React.FC<AddTeamMembersProps> = ({
  onClose,
  onAddMember,
  teamMembers,
}) => {
  const [selectedMembers, setSelectedMembers] = useState<memberInterface[]>([]);
  const [search, setSearch] = useState("");
  const [membersToBeAdded, setMembersToBeAdded] = useState<memberInterface[]>([]);
  const [filteredMembers, setFilteredMembers] = useState<memberInterface[]>([]);
  const [showUserList, setShowUserList] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const fetchMembers = async (searchTerm: string) => {
    if (!searchTerm.trim()) {
      setFilteredMembers([]);
      setShowUserList(false);
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const token = localStorage.getItem("token");
      if (!token) {
        throw new Error("No authentication token found");
      }

      const response = await axios.get(
        `${process.env.NEXT_PUBLIC_API_URL}/users/search?name=${searchTerm}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      // Filter out already selected and added members from API response
      const availableMembers = response.data.filter((member: memberInterface) => {
        const isNotSelected = !selectedMembers.some(m => m.id === member.id);
        const isNotAdded = !membersToBeAdded.some(m => m.id === member.id);
        const isNotInTeam = !teamMembers.some(m => m.id === member.id);
        return isNotSelected && isNotAdded && isNotInTeam;
      });

      setFilteredMembers(availableMembers);
      setShowUserList(true);
    } catch (err) {
      console.error("Error fetching members:", err);
      setError(err instanceof Error ? err.message : "Failed to fetch members");
      setFilteredMembers([]);
    } finally {
      setIsLoading(false);
    }
  };

  // Debounced search effect
  useEffect(() => {
    const delayDebounceFn = setTimeout(() => {
      if (search.trim()) {
        fetchMembers(search);
      }
    }, 500);

    return () => clearTimeout(delayDebounceFn);
  }, [search]);

  const handleSelectMember = (member: memberInterface) => {
    if (!selectedMembers.some(m => m.id === member.id)) {
      setSelectedMembers(prev => [...prev, member]);
    }
    setSearch("");
    setShowUserList(false);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value);
    if (e.target.value.trim()) {
      setShowUserList(true);
    }
  };

  const handleRemoveSelectedMember = (memberId: string) => {
    setSelectedMembers(prev => prev.filter(m => m.id !== memberId));
  };

  const handleRemoveAddedMember = (memberId: string) => {
    setMembersToBeAdded(prev => prev.filter(m => m.id !== memberId));
  };

  const handleSelectMember = (member: memberInterface) => {
    if (!selectedMembers.some(m => m.id === member.id)) {
      setSelectedMembers(prev => [...prev, member]);
    }
    // Don't clear search or hide dropdown - allow for more selections
    setSearch("");
    // Keep the dropdown visible
    setShowUserList(true);
  };
  
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value);
    setShowUserList(true);
  };
  
  const handleAddMember = () => {
    if (selectedMembers.length === 0) return;
  
    // Add all selected members to the list
    setMembersToBeAdded(prev => {
      // Filter out any duplicates
      const newMembers = selectedMembers.filter(
        selected => !prev.some(existing => existing.id === selected.id)
      );
      return [...prev, ...newMembers];
    });
  
    // Clear selections after adding
    setSelectedMembers([]);
    setSearch("");
    setShowUserList(false);
  };

  const handleRoleSelect = (memberId: string, role: string) => {
    setMembersToBeAdded(prev => {
      const updatedMembers = prev.map(member =>
        member.id === memberId ? { ...member, role } : member
      );
      
      const updatedMember = updatedMembers.find(m => m.id === memberId);
      if (updatedMember) {
        onAddMember(updatedMember);
      }
      
      return updatedMembers;
    });
  };

  return (
    <div className="fixed inset-0 flex items-center justify-center h-auto bg-black/50">
      <div className="bg-white rounded-xl h-[500px] shadow-lg w-[480px] p-5 relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-gray-900">
            Add Team Members
          </h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Search and Add Section */}
        <div className="flex relative w-full">
          <div className="flex items-center w-full gap-2">
            <div className="flex flex-wrap flex-grow p-0.5 overflow-y-auto border-2 rounded-lg min-h-[40px] max-h-auto">
              {selectedMembers.map((member) => (
                <div
                  key={member.id}
                  className="flex items-center m-0.5 gap-2 bg-[#5D56BD] rounded-full px-2"
                >
                  <img
                    src={member.avatar || "/api/placeholder/40/40"}
                    alt=""
                    className="w-5 h-5 rounded-full"
                  />
                  <div className="text-xs">
                    <p className="font-medium text-[#FFFFFF] leading-none">
                      {member.fullName}
                    </p>
                  </div>
                  <button
                    onClick={() => handleRemoveSelectedMember(member.id)}
                    className="text-[#FFFFFF] hover:text-red-500"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              ))}

              <input
                type="text"
                placeholder="Add others by name"
                className="flex-grow px-3 py-2 min-w-[120px] text-sm border-none outline-none"
                onChange={handleInputChange}
                value={search}
                onClick={() => {
                  if (search.trim()) {
                    setShowUserList(true);
                  }
                }}
              />
            </div>
            <button
              onClick={handleAddMember}
              disabled={selectedMembers.length === 0}
              className={`px-6 py-2 text-white rounded-lg text-sm font-medium ${
                selectedMembers.length === 0
                  ? "bg-gray-400 cursor-not-allowed"
                  : "bg-[#5D56BD] hover:bg-indigo-700"
              }`}
            >
              Add
            </button>
          </div>
        </div>

        {/* Dropdown List */}
        <div className="relative">
          {showUserList && (
            <div className="mt-1 absolute border-2 rounded-lg bg-white text-sm w-[358px] z-50 shadow-lg max-h-48 overflow-y-auto">
              {isLoading && (
                <div className="p-4 text-center text-gray-500">Loading...</div>
              )}
              {error && (
                <div className="p-4 text-center text-red-500">{error}</div>
              )}
              {!isLoading && !error && filteredMembers.length === 0 && search && (
                <div className="p-4 text-center text-gray-500">
                  No members found
                </div>
              )}
              {!isLoading &&
                filteredMembers.map((member) => (
                  <div
                    key={member.id}
                    className="flex items-center px-3 py-2 hover:bg-gray-50 cursor-pointer"
                    onClick={() => handleSelectMember(member)}
                  >
                    <img
                      src={member.avatar || "/api/placeholder/40/40"}
                      alt={member.fullName}
                      className="w-8 h-8 rounded-full mr-3"
                    />
                    <div>
                      <div className="text-sm font-medium">{member.fullName}</div>
                      {member.position && (
                        <div className="text-xs text-gray-500">
                          {member.position}
                        </div>
                      )}
                    </div>
                  </div>
                ))}
            </div>
          )}
        </div>

        {/* Added Members List */}
        <div className="absolute mt-4 w-[430px] overflow-x-hidden z-0">
          <div className="flex text-sm justify-between text-gray-500 mb-4 z-0">
            <span>Team members ({membersToBeAdded.length})</span>
            <span className="mr-20">Roles</span>
          </div>

          <div className="space-y-4">
            {membersToBeAdded.map((member) => (
              <div
                key={member.id}
                className="flex items-center justify-between cursor-pointer"
              >
                <div className="flex items-center gap-3">
                  <img
                    src={member.avatar || "/api/placeholder/40/40"}
                    alt={member.fullName}
                    className="w-10 h-10 rounded-full p-0.5"
                  />
                  <div>
                    <p className="text-sm font-medium text-gray-900">
                      {member.fullName}
                    </p>
                    <p className="text-sm text-gray-500">{member.position}</p>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <select
                    className="px-3 py-1 text-sm border border-gray-200 rounded-lg"
                    value={member.role || ""}
                    onChange={(e) => handleRoleSelect(member.id, e.target.value)}
                  >
                    <option value="">Select Role</option>
                    <option value="owner">Owner</option>
                    <option value="admin">Admin</option>
                    <option value="user">User</option>
                    <option value="client">Client</option>
                  </select>
                  <button
                    onClick={() => handleRemoveAddedMember(member.id)}
                    className="text-gray-400 hover:text-red-500"
                  >
                    <X className="w-5 h-5" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddTeamMembers;